@import "../components/variables";

@media screen and (max-width: 1500px), screen and (max-device-width: 1500px){
  body{
    .personal__cabinet{
      .container{
        .content__cabinet{
          .profiles{
            .custumer__contacts{
              .contacts{
                .value{
                  form{
                    .contactsSave{
                      margin: 40px 0 30px -95px;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1310px), screen and (max-device-width: 1310px){
  body{
    .personal__cabinet{
      .container{
        .content__cabinet{
          grid-template-columns: 35% 65%;
          .profiles{
            .custumer__contacts{
              .contacts{
                .value{
                  form{
                    .contactsSave{
                      margin: 40px 0 30px -85px;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1150px), screen and (max-device-width: 1150px){
  body{
    .personal__cabinet{
      .container{
        .content__cabinet{
          .profiles{
            .custumer__contacts{
              .contacts{
                .key{
                  p{
                    line-height: 2.5em;
                  }
                }
                .value{
                  p{
                    line-height: 2.5em;
                  }
                  form{
                    .contactsSave{
                      margin: 40px 0 30px -75px;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1100px), screen and (max-device-width: 1100px){
  body{
    .personal__cabinet{
      .container{
        .content__cabinet{
          grid-template-areas: "headline headline"
                               "nav nav"
                               "tabs tabs";
          ul{
            display: flex;
            justify-content: space-between;
            li{
              width: 30%;
            }
          }
          .profiles{
            width: 100%;
          }
          .myOrders{
            width: 100%;
            ul{
              flex-direction: column;
            }
          }
        }
      }
    }
  }
  .orderInfo{
    width: 500px!important;
  }
}

@media screen and (max-width: 600px), screen and (max-device-width: 600px){
  body{
    .orderInfo{
      width: 290px!important;
      ul{
        li{
          div{
            flex-wrap: wrap;
            justify-content: space-around;
            .value{
              margin: 10px 0;
              width: 100%;
              font-size: 1.25em;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 533px), screen and (max-device-width: 533px){
  body{
    .personal__cabinet{
      .container{
        .content__cabinet{
          .featuredProducts{
            justify-content: center;
            div{
              width: 90%;
              a{
                img{
                  width: 200px;
                  height: 200px;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 510px), screen and (max-device-width: 510px){
  body{
    .personal__cabinet{
      .container{
        .content__cabinet{
          .profiles{
            .custumer__contacts,
            .custumer__email,
            .custumer__password{
              .contacts{
                .value{
                  form{
                    input{
                      width: 200px;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 450px), screen and (max-device-width: 450px){
  body{
    .personal__cabinet{
      .container{
        .content__cabinet{
          .profiles{
            .custumer__contacts{
              .contacts{
                .key{
                  p{
                    margin: 0 5px 0 30px;
                  }
                }
                .value{
                  form{
                    p{
                      margin: 0 0 0 5px;
                    }
                    input{
                      width: 150px;
                    }
                    .contactsSave{
                      margin: 40px 0 30px -55px;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}