@import "../components/variables";

@media screen and (max-width: 1500px), screen and (max-device-width: 1500px){
  body{
    .busket{
      .container{
        .content__howToMakeOrder{
          margin: 85px 0;
        }
      }
    }
  }
}

@media screen and (max-width: 1310px), screen and (max-device-width: 1310px){
  body{
    .busket{
      .container{
        .content__howToMakeOrder{
          margin: 75px 0;
        }
      }
    }
  }
}

@media screen and (max-width: 1150px), screen and (max-device-width: 1150px){
  body{
    .busket{
      .container{
        .content__howToMakeOrder{
          margin: 65px 0;
          justify-content: flex-start;
        }
      }
    }
  }
}

@media screen and (max-width: 1050px), screen and (max-device-width: 1050px){
  body{
    .busket{
      .container{
        .content__busket{
          .costumerInfo{
            form{
              p{
                width: 200px;
                input{
                  width: 100%;
                }
              }
              textarea{
                width: 386px;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 940px), screen and (max-device-width: 940px){
  body{
    .busket{
      .container{
        grid-template-areas: "breadcrumb breadcrumb"
                             "busket busket"
                             "order order";
        margin-bottom: 0;
        .content__busket{
          margin-right: 0;
        }
        .content__howToMakeOrder{
          flex-direction: row;
          justify-content: space-between;
          div{
            width: 32%;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 800px), screen and (max-device-width: 800px){
  body{
    .busket{
      .container{
        .content__howToMakeOrder{
          flex-direction: column;
          div{
            width: 100%;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 700px), screen and (max-device-width: 700px){
  body{
    #thanksOrder{
      width: 290px;
      button{
        width: 75%;
      }
    }
  }
}

@media screen and (max-width: 550px), screen and (max-device-width: 550px){
  body{
    .busket{
      .container{
        .content__busket{
          ul{
            li{
              div{
                flex-wrap: wrap;
                justify-content: space-around;
                .value{
                  margin: 0 0 10px;
                  width: 100%;
                  font-size: 1.25em;
                  text-align: center;
                }
              }
            }
          }
          .costumerInfo{
            form{
              p,
              textarea{
                margin: 10px 0;
                width: 90%;
                display: block;
              }
              p{
                .asterisk{
                  right: 0;
                }
              }
              button{
                width: 200px;
              }
            }
          }
        }
      }
    }
  }
}